@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');
html {
  background-color: #202225;
}

strong {
  font-weight: 900;
  font-family: serif;
}

.App {
  height: 100%;
  width: 100%;
  position: relative;
  text-align: center;
}

.stage {
  user-select: none;
  padding: 8px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 8px;
  min-height: 10px;
  color: white;
  font-size: 15px;
  text-align: center;
  font-weight: 300;
  transition: transform 0.25s, box-shadow 0.4s;
  justify-content: center;
  align-items: center;
  width: auto;
  max-width: 150px !important;
}

.stage:hover {
  transform: scale(1.02);
  box-shadow: 0.0px 8.0px 24.0px 0px rgba(0, 0, 0, 0.1) !important;
}

.stage:active:hover {
  transform: scale(1);
}

.stage:hover .leftBtn {
  display: block;
}

.stage:hover .addStageBtn {
  display: block;
}

#itemContent {
  border: 1px dashed rgba(255, 255, 255, 0);
  position: relative;
  /* margin-bottom: 0; */
}

#itemContent:hover {
  /* border: 1px dashed white; */
}

#itemContent:hover #editTextBtn {
  display: inline-block;
}

#itemContentAutomation {
  /* border: 1px dashed rgba(255,255,255,0); */
  position: relative;
  /* margin-bottom: 0; */
}

#itemContentAutomation:hover {
  /* color: #1F76C2; */
}

#itemContentAutomation:hover #editTextBtn {
  display: inline-block;
}

#editTextBtn {
  width: 13.29px;
  height: 13.29px;
  padding-right: 16px;
  background-color: rgba(0, 0, 0, 0);
  font-size: 1px;
  top: 0;
  right: 0;
  border: none;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  display: none;
