.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms linear;
}

.sideBarIn-enter {
  transform: translateX(130px);
}

.sideBarIn-enter.sideBarIn-enter-active {
  transform: translateX(0);
  transition: transform 150ms linear;
}

.sideBarOut-enter {
  transform: translateX(-130px);
}

.sideBarOut-enter.sideBarOut-enter-active {
  transform: translateX(0);
  transition: transform 150ms linear;
}

.sideMenuBarIn-enter {
  transform: translateX(225px);
}

.sideMenuBarIn-enter.sideMenuBarIn-enter-active {
  transform: translateX(0px);
  transition: transform 150ms linear;
}

.sideMenuBarOut-enter {
  transform: translateX(-225px);
}

.sideMenuBarOut-enter.sideMenuBarOut-enter-active {
  transform: translateX(0px);
  transition: transform 150ms linear;
}

.my-node-enter {
  opacity: 0;
}

.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}

.my-node-exit {
  opacity: 1;
}

.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

