.triangle {
  width: 0px;
  height: 0px;
  border-style: inset;
  border-width: 0 9.375px 16.2375px 9.375px;
  border-color: transparent transparent #0085FF transparent;
  float: left;
  transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -o-transform: rotate(360deg);
  margin: 24px;
  margin-right: 10px;
  margin-bottom: 8px;
}
.triangle p {
  text-align: center;
  top: 1px;
  left: -46.3px;
  position: relative;
  width: 93px;
  height: 93px;
  margin: 0px;
  color: #FFFFFF;
  font-size: 13px;
}

