.dot {
  height: 5px;
  width: 5px;
  background-color: #bbb;
  border-radius: 50%;
  display: block;
  margin: 2px;
}
.dotWarning{
  animation: pulse 2s infinite;
  background-color: rgba(238,210,2, 1);
  height: 5px;
  width: 5px;
  border-radius: 50%;
}
.dotWarning p{
  font-size: 4px;
  color: #FFFFFF;
  margin-top: 0px;

}
@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(238,210,2, 0.7);
  }

  70% {
    transform: scale(5);
    box-shadow: 0 0 0 2px rgba(238,210,2, 0);
  }

  100% {
    transform: scale(1);
  }
}

